.gridder {
  margin: 0;
  padding: 0;
  font-size: 0;
  list-style-type: none;
}

.gridder-list, .gridder-show {
  font-size: 16px;
}

.gridder-list {
  vertical-align: top;
  display: inline-block;
}

.gridder-show {
  float: left;
  width: 100%;
  display: block;
  position: relative;
}

.gridder-content {
  display: none;
}

.gridder-list {
  width: 49.5%;
}

.gridder-list:nth-child(n) {
  margin-bottom: 1%;
  margin-right: 1%;
}

.gridder-list:nth-of-type(2n) {
  margin-bottom: 0;
  margin-right: 0;
}

